import React from 'react';
import "../css/navbar.css";
import { NavLink } from "react-router-dom";
const Navbar = (props) => {
    return (
        <nav>
        <ul>
            <li><NavLink onClick={() => props.changeColor('#263238')} to="/home" className="nav-link"><i class="fa-solid fa-house"></i> Home</NavLink> </li>
            <li><NavLink onClick={() => props.changeColor('#263238')} to="/Signup" className="nav-link"><i class="fa-solid fa-user-plus"></i> Signup</NavLink></li>
            <li><NavLink onClick={() => props.changeColor('#263238')} to="/Login" className="Nav-link"><i class="fa-solid fa-right-to-bracket"></i> Login</NavLink></li>
            <li><NavLink onClick={() => props.changeColor('#263238')} to="/Calculator" className="nav-link"><i class="fa-solid fa-calculator"></i> Calculator</NavLink></li>
            <li><NavLink onClick={() => props.changeColor('#263238')} to="/emplist" className="nav-link"><i class="fa-solid fa-list"></i> Employeelist</NavLink></li>
            <li><NavLink onClick={() => props.changeColor('#263238')} to="/pagination" className="nav-link"><i class="fa-solid fa-book"></i> Pagination</NavLink></li>
            <li><a to=""><i class="fa-regular fa-file"></i> Dummy</a></li>
        </ul>
        </nav>
    )
}
 
export default Navbar;