import React from "react";
import "../css/signup.css";
import { useForm } from "react-hook-form";


const Signup = () => {
  const { register, handleSubmit,formState:{errors} } = useForm();

  const onSubmit = (data)=> {
    console.log(data);
  }; 

  return (
    <div id="signupForm">
      <form onSubmit={handleSubmit(onSubmit)}>

      <div class="WELCOME">
        <div class="WELCOME">
          <label class="required"><i class="fa-solid fa-user"></i> USERNAME</label>
          <input type="text" maxlength="16" {...register("username",{
            required:{
              value:true,
              message:'username is required'
            },
            minLength:{
              value:3,
              message:'username should have atleast 3 charcters' 
            },
            maxlenght:{
              value:15,
              message:'username should have only 15 characters'
            }
           }
            )}/>
            <small>{errors.username?.message}</small>
        </div>
        <div class="WELCOME">
          <label class="required"><i class="fa-solid fa-lock"></i> PASSWORD</label>
          <input type="password" maxlength="10" {...register("password",{
          pattern:{
            value:/^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,}$/,
            message:"Minimum eight characters, at least one uppercase letter, one lowercase letter, one number and one special character"
          }
      })} />
          <small>{errors.password?.message}</small>
        </div>
        <div class="WELCOME">
          <label class="required"><i class="fa-solid fa-lock"></i> RETYPE-PASSWORD</label>
          <input type="password" maxlength="10" {...register("re-password",{
             pattern:{
              value:/^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,}$/,
              message:"Minimum eight characters, at least one uppercase letter, one lowercase letter, one number and one special character"
            }
          })}/>
          <small>{errors.password?.message}</small>

        </div>
        <button class="SIGNUP"><i class="fa-solid fa-arrow-right-to-bracket"></i> SIGNUP</button>
 
      </div>
      </form>
    </div>
  );
};
export default Signup;
