import React from "react";
import "../css/login.css";
import { useForm } from "react-hook-form";
import Swal from 'sweetalert2';
const Login = () => {
  const { register, handleSubmit,formState:{errors} } = useForm();

  const onSubmit = (data) => {
    console.log(data);
    Swal.fire({
      toast:true,
      position: "top-end",
      icon: "success",
      title: "Your work has been saved",
      showConfirmButton: false,
      timer: 1500
    })
  };
  return (
    <div id="loginform">
      <form onSubmit={handleSubmit(onSubmit)}>
      <div class="hello">
        <label class="required ">
          <i class="fa-solid fa-user"></i> USERNAME
        </label>
        <input type="text" maxlength="16" {...register("username",{
          required:{
            value:true,
            message:'username is required'
          },
          minLength:{
            value:3,
            message:'username should have atleast 3 characters'
          },
          maxLength:{
            value:15,
            message:'username should have only 15 characters'
          }
})} />
<small>{errors.username?.message}</small>
      </div>
      <div class="hii">
        <label class="required">
          <i class="fa-solid fa-lock"></i> PASSWORD
        </label>
        <input type="password" maxlength="10" {...register("password",{
          pattern:{
            value:/^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,}$/,
            message:"Minimum eight characters, at least one uppercase letter, one lowercase letter, one number and one special character"
          }
          
        })} />
        <small>{errors.password?.message}</small>
      </div>
      <div>
        <button class="button"><i class="fa-solid fa-arrow-right-to-bracket"></i> LOGIN</button>
      </div>
      </form>
    </div>
  );
};

export default Login;
