import React from "react";
import "../css/home.css";
import profile from "../assets/images.png";
import logo from "../assets/logo.png";
import logot from "../assets/logosecond.png";

const Home = () => {
  return (
    <div id="Home">
      <div class="imgsecond">
        <img src={logo} className="myLogo" alt="images" />
      </div>
      <div class="pro">
        <img src={profile} alt="images" />
      </div>

      <div class="element">
        <p>
          <h3>HI THERE I'M </h3>
        </p>
        <div class="name">
          <p>
            <h1>VISHWA V</h1>
          </p>
        </div>
        <div class="info">
          <p>
            <h4>ENTRY-LEVEL WEB DESIGNER </h4>
          </p>
        </div>
        <div class="contact">
          <p>
            <h2>
              CONTACT ME <i class="fa-solid fa-down-long"></i>{" "}
            </h2>
          </p>
          <div class="mail">
            <p>
              <i class="fa-solid fa-envelope"></i> VISHWA2362001@GMAIL.COM{" "}
            </p>
          </div>
          <div class="VIEW CV">
            <button>
            <a href="/VISHWA_CV.pdf" target="_blank">
           <i class="fa-solid fa-download"></i> VIEW CV
           </a>
           </button>
          </div>
            
          <div class="about">
            <p>
              TO KNOW MORE ABOUT ME <i class="fa-solid fa-turn-down"></i>{" "}
            </p>
          </div>
          <div class="aboutme">
            <h1>ABOUT ME :</h1>
          </div>
          <div class="aboutdown">
            <p>
              Hey there! I'm VISHWA.V, a recent MCA graduate with a strong
              foundation in web development. I'm proficient in HTML, CSS,
              JavaScript, React.js. In my projects, I've developed a responsive
              e-commerce site using React.js . Beyond hands-on work, I am
              passionate about sharing my insights and experiences within the
              web development realm. You can find a collection of my thoughts
              and analyses on my Portfolio Link, where I explore the latest
              trends.
            </p>
            <p>
              I am enthusiastic about the opportunity to contribute my skills
              and passion to a dynamic web development team. Connect with me on
              LinkedIn or drop me an email that I mentioned above. Thank you for
              considering my application!
            </p>
          </div>
          <div class="logot">
            <img src={logot} alt="images" />
          </div>
        </div>
      </div>
    </div>
  );
};

export default Home;
