import React from "react";
import profile from "../assets/img.png";
import "../css/emplist.css";

const Card = (props) => {
  return (
    <div class="box">
      <img src={profile} alt="images" />
      <div class="table">
        <p>
          <i class="fa-regular fa-user"></i> Username :{props.emp.Name}
        </p>
        <p>
          <i class="fa-solid fa-user-tie"></i> Id No :{props.emp.EmployeeID}
        </p>
      </div>

      <button onClick={() => props.btnfn()} id="view-btn">{props.btntext}</button>
      
    </div>
  );
};
export default Card;
